<span 
  class="monospace-massure"
  bind:this={meassure}
  >X</span>
<div id="sourceParent">
  <pre
    class="highlight"
    style="padding-left: { lnWidth + leftPadding }px"
    bind:this={pre}
  >
  {#each codeInfo.tokens as token}
    <span 
     class={token.name}
    >{token.text}</span>
  {/each}
  </pre>
  <div 
    class="numbers" 
    style="width: { lnWidth }px"
    bind:this={num}
  >
    {#each [...Array(codeInfo.lines).keys()] as l}
      <span
        class={(l >= codeInfo.selectionStartLine) && (l <=
        codeInfo.selectionEndLine)? 'selected': ''}
      >{ leftPad(l + 1, lnDigits, ' ') }</span>
    {/each}
  </div>
  <textarea
    spellcheck="false"
    wrap="off"
    style="padding-left: { lnWidth + leftPadding }px"
    bind:value={value}
    bind:this={textarea}
    on:scroll={updateScrollPosition}
    ></textarea>
<!-- 
-->
</div>
<script>
  import { onMount } from 'svelte'
  import { tokenize } from './highlight.js' 
  import { leftPad } from './helpers.js'
  
  export let value = ''

  /* Elements */
  let meassure
  let pre
  let num
  let textarea

  /* Line Numbering */
  const lineHeight = 20
  let leftPadding = 4
  let charWidth = 11
  
  /* Selection */
  let selectionStartChar = -1
  let selectionEndChar = -1

  $: codeInfo = tokenize(value, selectionStartChar, selectionEndChar)
  $: lnDigits = codeInfo.lines.toString().length
  $: lnWidth = lnDigits * charWidth + 12
  
  function updateSelection() {
    const activeElement = document.activeElement
  
    // make sure this is your textarea
    if (activeElement === textarea) {
      selectionStartChar = textarea.selectionStart
      selectionEndChar = textarea.selectionEnd
    }
  }

  document.addEventListener('selectionchange', updateSelection)

  function updateScrollPosition() {
    pre.scrollLeft = textarea.scrollLeft
    pre.scrollTop = textarea.scrollTop
    num.scrollTop = textarea.scrollTop
  }

  onMount(function() {
    charWidth = meassure.clientWidth
  })
</script>

<style type="text/sass">

#sourceParent
  width: 100%
  height: 100%
  background: $bg-dark

textarea
  display: block
  outline: 1px solid $bg-dark
  background: transparent
  cursor: text
  color: transparent
  caret-color: $fg
  resize: none


textarea,
pre
  overflow-x: auto
  position: absolute
  top: 0px
  left: 0px
  width: 100%
  height: 100%
  z-index: 8
  border: none
  line-height: $lineheight
  font-size: $fontsize
  vertical-align: baseline

pre
  &::-webkit-scrollbar-track,
  &::-webkit-scrollbar-thumb,
  &::-webkit-scrollbar-corner
    background: none 
 
.numbers
  overflow-x: auto
  position: absolute
  top: 0px
  left: 0px
  height: 100%
  z-index: 10
  font-size: $fontsize - 2
  background: $bg-light
  color: $mangool
  line-height: $lineheight
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
  &::-webkit-scrollbar
    display: none;
  span
    float: left 
    clear: both
    width: 100%
    text-align: right
    padding-right: 4px
    box-shadow: inset 0 0 2px black
    vertical-align: baseline
    &.selected
      background: $jingool
      
.monospace-massure
  position: absolute
  top: -100px
  left: -100px
  line-height: $lineheight
  font-size: $fontsize
  font-family: monospace
</style>
